body.home {
  /**
    Just some basic styling for the closing trigger
  **/ }
  body.home .header-video {
    position: relative;
    overflow: hidden;
    clear: both; }
    body.home .header-video::after {
      clear: both;
      content: "";
      display: block; }
    body.home .header-video img {
      height: 100%; }
  body.home .header-video iframe,
  body.home .header-video video {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; }
  body.home .header-video iframe {
    height: 100%;
    width: 100%; }
  body.home .header-video video {
    width: 100%; }
  body.home .header-video__teaser-video {
    width: 100%;
    height: auto; }
  body.home .header-video__media {
    width: 100%;
    height: auto; }
  @media only screen and (min-width: 992px) {
    body.home .headline {
      width: calc(37.5% - 23.375px);
      float: left;
      margin-left: 17px;
      margin-left: calc(6.25% - 18.0625px + 34px); } }
  @media (min-width: 992px) {
    body.home .headline {
      position: absolute;
      top: 50%;
      -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      transform: translateY(-50%);
      font-size: 63px;
      line-height: 1;
      letter-spacing: 2px;
      color: #FFFFFF;
      text-transform: uppercase; }
      body.home .headline:hover {
        cursor: pointer; } }
  body.home .headline a {
    display: block;
    text-decoration: none;
    padding: 0 3rem 3rem 0; }
    body.home .headline a:hover {
      background-color: transparent; }
  body.home .headline span {
    display: inline-block;
    text-transform: uppercase;
    color: #fff;
    letter-spacing: 2px;
    font-size: 18px;
    line-height: 1.16;
    border-bottom: 1px solid #a45833; }
  body.home .header-video__close-trigger {
    z-index: 99;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.2);
    text-align: center;
    color: white;
    text-decoration: none;
    font-size: 20px;
    letter-spacing: 1px;
    line-height: 1;
    padding: .5rem 1rem;
    right: 30px;
    top: 30px;
    -webkit-transition: background .3s;
    -moz-transition: background .3s;
    transition: background .3s;
    border: none;
    text-transform: uppercase; }
  body.home .header-video__close-trigger:hover {
    background-color: rgba(0, 0, 0, 0.4);
    cursor: pointer; }

